"use client";

import { usePathname, useRouter } from "next/navigation";
import { useState } from "react";
import SearchBar from "@/components/SearchBar";
import CartIcon from "@/components/CartIcon";
import { useLanguage } from "@/context/LanguageContext";
import { useCart } from "@/context/CartContext";

/* cookie helper */
function setCookie(name: string, value: string, days = 365) {
  const expires = new Date(Date.now() + days * 864e5).toUTCString();
  document.cookie = `${name}=${encodeURIComponent(value)}; expires=${expires}; path=/`;
}

export default function TopNavbar() {
  const pathname = usePathname() || "/";
  const router = useRouter();

  const [activeMode, setActiveMode] = useState<
    "Scheduled" | "Now" | "Rapid" | "Electronics"
  >("Now");

  const isSearchPage = pathname === "/search";

  const { lang, setLang } = useLanguage();
  const { items } = useCart();

  const showBack =
    pathname !== "/" &&
    (pathname.startsWith("/product") ||
      pathname.startsWith("/subcategory") ||
      isSearchPage);

  const cartCount = Array.isArray(items)
    ? items.reduce((s: number, i: any) => s + Number(i.qty ?? 1), 0)
    : 0;

  const placeholder =
    lang === "so" ? "Ka raadi 50,000+ alaab" : "Search 50,000+ items";
  const isHome = pathname === "/";

  return (
    <header className="sticky top-0 z-50 bg-gradient-to-b from-[#ffe4dc] via-[#ffe4dc] to-[#ffffff]">
      <div className="mx-auto max-w-md px-3 pt-2">
        {isHome && !isSearchPage && (
          <div className="mb-2 flex items-center gap-2">
            {["Scheduled", "Now", "Rapid", "Electronics"].map((label) => {
              const isActive = label === activeMode;
              return (
                <button
                  key={label}
                  onClick={() =>
                    setActiveMode(
                      label as "Scheduled" | "Now" | "Rapid" | "Electronics"
                    )
                  }
                  className={`flex-1 h-8 rounded-full text-xs font-semibold border ${
                    isActive
                      ? "bg-[#0B6EA9] text-white border-[#0B6EA9]"
                      : "bg-white/90 text-[#0B6EA9] border-white"
                  }`}
                >
                  {label}
                </button>
              );
            })}
          </div>
        )}
        {/* ===== ROW 1 ===== */}
        <div className="flex items-center gap-2">
          {showBack && (
            <button
              onClick={() =>
                window.history.length > 1 ? router.back() : router.push("/")
              }
              className="h-10 w-10 rounded-full bg-[#0A5F91] text-white grid place-items-center"
              aria-label="Back"
            >
              ‚Üê
            </button>
          )}

          {/* ‚úÖ HIDE SEARCH BAR ON /search */}
          {!isSearchPage && <SearchBar placeholder={placeholder} />}

          {/* Cart */}
          <div className="relative">
            <CartIcon />
            {cartCount > 0 && (
              <div className="absolute -top-1 -right-1 h-5 min-w-[18px] px-1 rounded-full bg-white text-[#0B6EA9] text-[11px] font-bold grid place-items-center">
                {cartCount}
              </div>
            )}
          </div>
        </div>

        {/* ===== ROW 2 ===== */}
        <div className="mt-1 pb-2 flex items-center justify-end text-[#0B6EA9]">
          <div className="flex items-center gap-2">
            {/* Language */}
            <div className="flex bg-white rounded-full p-1 shadow-sm">
              <button
                onClick={() => {
                  setLang("so");
                  setCookie("lang", "so");
                }}
                className={`px-2 py-1 text-[11px] rounded-full ${
                  lang === "so"
                    ? "bg-[#0B6EA9] text-white"
                    : "text-[#0B6EA9]/80"
                }`}
              >
                üá∏üá¥
              </button>
              <button
                onClick={() => {
                  setLang("en");
                  setCookie("lang", "en");
                }}
                className={`px-2 py-1 text-[11px] rounded-full ${
                  lang === "en"
                    ? "bg-[#0B6EA9] text-white"
                    : "text-[#0B6EA9]/80"
                }`}
              >
                üá¨üáß
              </button>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
}
